import { Component, OnInit } from '@angular/core';
import { AlertController, ToastController } from '@ionic/angular';
import { Storage } from '@ionic/storage';

@Component({
    selector: 'app-user-settings',
    templateUrl: './user-settings.page.html',
    styleUrls: ['./user-settings.page.scss'],
})
export class UserSettingsPage implements OnInit {

    public user:any;
    constructor(private storage:Storage, public toastController: ToastController, public alertController: AlertController) { 
        
        this.user = {};
        this.storage.get("datanow_user").then((data) => {
            this.user = data;
        })
        
    }

    ngOnInit() {
    }


    
    async changeSetting(name, title, inputType) {
        
        let inputs = [
                {
                    name: name,
                    type: inputType,
                    placeholder: title,
                    value: name !== "password" ? this.user[name] : ""
                }
            ];
            
        if (name === "password"){
            inputs.unshift({
                    name: "currentPassword",
                    type: inputType,
                    placeholder: "Current Password",
                    value: ""
                })
        }    
           
           
           
        
        
        const alert = await this.alertController.create({
            header: 'Change ' + title,
            inputs: inputs,
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    cssClass: 'secondary',
                    handler: () => {
                    }
                }, {
                    text: 'Save',
                    handler: (data) => {
                        this.user[name] = data[name];

                        this.presentToast();

                    }
                }
            ],
            mode: "md"
        });

       await alert.present();
     } 
     
     
     private async presentToast(){
        const toast = await this.toastController.create({
            message: '<ion-icon name="md-checkmark"></ion-icon> Changes Saved',
            duration: 2000,
            color:'dark',
            mode:'md'
        });
        toast.present();         
     }  


}
